<template>
  <v-container
    id="user-profile-view"
    fluid
    tag="section"
  >
  <div class="absolute z-20 -translate-y-16 left-1/3 -translate-x-10 hidden slim">
    <material-alert
      color="success"
      class="text-xs"
      dark
    >
      <span class="text-caption font-weight-medium">Success Uploaded</span> — File has uploaded
      <v-btn
        icon
        color="white"
        @click="close"
      >
        <i class="fa-solid fa-circle-xmark text-xl"></i>
      </v-btn>
    </material-alert>
  </div>
    <v-card
      class="w-full h-96 p-4"
      shaped
    >
      <v-card-text>
          <v-tabs
          v-model="muns"
          color="info"
          tab-transition
          centered
          align-with-title
        >
          <v-tabs-slider color="info"></v-tabs-slider>
          <v-tab>Uploader</v-tab>
          <v-tab>Console log()</v-tab>
        </v-tabs>
        <v-tabs-items v-model="muns">
          <v-tab-item
          key="1"
          >
            <v-card class="p-4 h-full hover:bg-gray-200"
              @click="swim"
            >
              <v-card-title
                class="text-h2 font-weight-regular p-2 flex justify-center text-red-600"
              >
              Drag & Drop File
              </v-card-title>
              <v-card-text>
                <div class="p-6 border border-red-600 border-dashed p-4 text-6xl rounded-lg hover:bg-rose-800 hover:bg-opacity-50 font-weight-regular grid place-items-center hover:text-white transition-all ease-in-out duration-200" contenteditable="true"><i class="fa-solid fa-cloud-arrow-up"></i></div>
              </v-card-text>
            </v-card>
          </v-tab-item>
          <v-tab-item
            key="2"
          >
            <v-card
            color="grey lighten-1"
            class="p-5 mt-5"
            >
              <v-card-subtitle
                class="border-b border-green-600"
              >
                Log()
              </v-card-subtitle>
              <v-card-text>
                <div class=" p-5 mt-3 grid overflow-y-auto h-36" contenteditable="true">
                  <v-row>
                    <v-col
                      v-for="(item, i) in filer"
                      :key="i"
                      cols="12"
                      md="12"
                    >
                      <div class="flex justify-between gap-2 p-1 text-green-600">
                        <div class="">(Log) {{item.name}}</div>
                        <div class="">{{item.lastModified}}</div>
                        <div class="">{{item.size}} Bytes</div>
                      </div>
                    </v-col>
                  </v-row>
                </div>
              </v-card-text>
            </v-card>
          </v-tab-item>
        </v-tabs-items>
      </v-card-text>
    </v-card>
    <div class="mb-16">
      <input type="file" class="uplod hidden" accept=".json,.doc,.docx,application/msword," multiple>
    </div>
  </v-container>
</template>
<script>
export default {
  data:() => {
    return{
      muns: 0,
      filer: [],
      sufi: '',
    }
  },
  methods: {
    swim(){
      var filo = document.querySelectorAll('.uplod')[0]
      filo.click()
      filo.addEventListener("input", () => {
          const dox = document.querySelectorAll(".slim")[0]
          if (filo.files.length >= 1) {
            filo.files.forEach((item, i) => {
              var mvl = [
                {
                  name: filo.files[i].name,
                  lastModified: filo.files[i].lastModified,
                  lastModifiedDate: filo.files[i].lastModifiedDate,
                  size: filo.files[i].size,
                }
              ]
            this.sufi = filo.files[i].name
            this.filer = this.filer.concat(mvl)
            dox.style.display = "block"
            })

          }
        })
        localStorage.setItem("files", JSON.stringify(this.filer))
    },
    close(){
      const dox = document.querySelectorAll(".slim")[0]
      dox.style.display = "none"
    }
  }
}
</script>